"use client";

import Image from "next/image";
import Link from "next/link";
import { TrendingUp, Users, DollarSign, Building2, Award, ArrowRight, BarChart3, Zap } from "lucide-react";

export default function EcosystemInsights() {
  // Static data for success stories
  const successStories = [
    {
      id: 1,
      title: "From Garage to Unicorn: How Mumbai FinTech Raised 2,500 Cr",
      excerpt: "A local fintech startup's journey from a small Mumbai office to becoming Maharashtra's latest unicorn, revolutionizing digital payments across India.",
      founder: "Rajesh Kumar",
      company: "PayMaharashtra",
      funding: "2,500 Cr",
      valuation: "Unicorn",
      location: "Mumbai",
      category: "FinTech",
      featuredImage: "/demo.png",
      slug: "paymaharashtra-unicorn-journey",
    },
    {
      id: 2,
      title: "Pune AI Startup Secures $50M Series B, Expands to 5 Countries",
      excerpt: "An artificial intelligence company from Pune closes major funding round and announces international expansion, creating 500+ jobs in Maharashtra.",
      founder: "Priya Deshmukh",
      company: "AI Innovations",
      funding: "$50M",
      valuation: "Series B",
      location: "Pune",
      category: "AI/ML",
      featuredImage: "/demo.png",
      slug: "ai-innovations-series-b",
    },
    {
      id: 3,
      title: "Nagpur AgriTech Startup Wins National Innovation Award",
      excerpt: "A rural-focused agritech startup receives recognition for innovative farming solutions that have helped 10,000+ farmers increase crop yields by 40%.",
      founder: "Vikram Patil",
      company: "FarmSmart Solutions",
      funding: "25 Cr",
      valuation: "Seed",
      location: "Nagpur",
      category: "AgriTech",
      featuredImage: "/demo.png",
      slug: "farmsmart-innovation-award",
    },
  ];

  // Statistics data
  const stats = [
    {
      icon: Building2,
      value: "15,000+",
      label: "Active Startups",
      color: "from-blue-500 to-blue-600",
      darkColor: "from-blue-600 to-blue-700",
    },
    {
      icon: DollarSign,
      value: "45,000 Cr",
      label: "Total Funding Raised",
      color: "from-green-500 to-green-600",
      darkColor: "from-green-600 to-green-700",
    },
    {
      icon: Users,
      value: "2.5 Lakh+",
      label: "Jobs Created",
      color: "from-purple-500 to-purple-600",
      darkColor: "from-purple-600 to-purple-700",
    },
    {
      icon: Award,
      value: "25+",
      label: "Unicorns",
      color: "from-orange-500 to-orange-600",
      darkColor: "from-orange-600 to-orange-700",
    },
  ];

  // Quick insights
  const insights = [
    {
      title: "Mumbai Leads in FinTech",
      description: "40% of Maharashtra's fintech startups are based in Mumbai, raising over 12,000 Cr in 2024.",
      icon: TrendingUp,
      color: "text-blue-500 dark:text-blue-400",
    },
    {
      title: "Pune: The AI Hub",
      description: "Pune has emerged as India's second-largest AI startup hub with 200+ companies in the space.",
      icon: Zap,
      color: "text-purple-500 dark:text-purple-400",
    },
    {
      title: "Rural Tech Growth",
      description: "AgriTech and rural-focused startups in Maharashtra have grown 150% in the last 2 years.",
      icon: BarChart3,
      color: "text-green-500 dark:text-green-400",
    },
  ];

  return (
    <section className="bg-gradient-to-br from-slate-50 via-white to-blue-50/30 dark:from-slate-900 dark:via-slate-800 dark:to-slate-900 border-b border-gray-200 dark:border-slate-700 py-16 relative overflow-hidden">
      {/* Decorative Background */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-0 right-0 w-96 h-96 bg-blue-200/20 dark:bg-blue-900/10 rounded-full blur-3xl"></div>
        <div className="absolute bottom-0 left-0 w-96 h-96 bg-orange-200/20 dark:bg-orange-900/10 rounded-full blur-3xl"></div>
      </div>

      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header Section */}
        <div className="text-center mb-12">
          <div className="inline-flex items-center gap-3 mb-4">
            <BarChart3 className="w-10 h-10 text-orange-500 dark:text-orange-400" />
            <h2 className="text-4xl md:text-5xl lg:text-6xl font-bold text-gray-900 dark:text-white">
              Ecosystem
              <span className="text-orange-500 dark:text-orange-400 ml-2">Insights</span>
            </h2>
          </div>
          <p className="text-gray-600 dark:text-gray-400 text-lg md:text-xl max-w-2xl mx-auto">
            Discover the numbers, stories, and trends shaping Maharashtra's startup landscape
          </p>
        </div>

        {/* Statistics Grid */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 md:gap-6 mb-16">
          {stats.map((stat, index) => {
            const Icon = stat.icon;
            return (
              <div
                key={index}
                className="bg-white dark:bg-slate-800 rounded-xl p-6 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 border border-gray-200 dark:border-slate-700"
              >
                <div className={`inline-flex p-3 rounded-lg bg-gradient-to-br ${stat.color} dark:bg-gradient-to-br ${stat.darkColor} mb-4`}>
                  <Icon className="w-6 h-6 text-white" />
                </div>
                <div className="text-2xl md:text-3xl font-bold text-gray-900 dark:text-white mb-1">
                  {stat.value}
                </div>
                <div className="text-sm text-gray-600 dark:text-gray-400 font-medium">
                  {stat.label}
                </div>
              </div>
            );
          })}
        </div>

        {/* Success Stories Section */}
        <div className="mb-12">
          <div className="flex items-center justify-between mb-8">
            <h3 className="text-2xl md:text-3xl font-bold text-gray-900 dark:text-white">
              Success Stories
            </h3>
            <Link
              href="/?category=success-stories"
              className="text-orange-500 dark:text-orange-400 font-semibold text-sm hover:underline flex items-center gap-1"
            >
              View All
              <ArrowRight size={16} />
            </Link>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {successStories.map((story) => (
              <Link
                key={story.id}
                href={`/post?slug=${story.slug}`}
                className="group bg-white dark:bg-slate-800 rounded-xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-300 hover:-translate-y-2 border border-gray-200 dark:border-slate-700"
              >
                {/* Image */}
                <div className="relative aspect-video overflow-hidden bg-gray-100 dark:bg-slate-700">
                  {story.featuredImage ? (
                    <Image
                      src={story.featuredImage}
                      alt={story.title}
                      fill
                      className="object-cover group-hover:scale-110 transition-transform duration-500"
                      sizes="(max-width: 768px) 100vw, 33vw"
                    />
                  ) : (
                    <div className="w-full h-full bg-gradient-to-br from-orange-400 to-orange-600 dark:from-orange-600 dark:to-orange-700"></div>
                  )}
                  <div className="absolute top-4 left-4">
                    <span className="inline-block px-3 py-1 bg-white/90 dark:bg-slate-900/90 backdrop-blur-sm text-orange-600 dark:text-orange-400 text-xs font-bold rounded-full">
                      {story.valuation}
                    </span>
                  </div>
                </div>

                {/* Content */}
                <div className="p-6">
                  <div className="flex items-center gap-2 mb-3">
                    <span className="text-xs font-bold text-orange-500 dark:text-orange-400 uppercase tracking-wide">
                      {story.category}
                    </span>
                    <span className="text-gray-400"></span>
                    <span className="text-xs text-gray-600 dark:text-gray-400">{story.location}</span>
                  </div>

                  <h4 className="text-lg font-bold text-gray-900 dark:text-white mb-2 group-hover:text-orange-500 dark:group-hover:text-orange-400 transition-colors line-clamp-2">
                    {story.title}
                  </h4>

                  <p className="text-sm text-gray-600 dark:text-gray-400 mb-4 line-clamp-3 leading-relaxed">
                    {story.excerpt}
                  </p>

                  <div className="border-t border-gray-200 dark:border-slate-700 pt-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <div className="text-xs text-gray-500 dark:text-gray-400 mb-1">Founder</div>
                        <div className="text-sm font-semibold text-gray-900 dark:text-white">{story.founder}</div>
                        <div className="text-xs text-gray-600 dark:text-gray-400">{story.company}</div>
                      </div>
                      <div className="text-right">
                        <div className="text-xs text-gray-500 dark:text-gray-400 mb-1">Funding</div>
                        <div className="text-sm font-bold text-green-600 dark:text-green-400">{story.funding}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        </div>

        {/* Quick Insights */}
        <div className="bg-white dark:bg-slate-800 rounded-2xl p-8 shadow-lg border border-gray-200 dark:border-slate-700">
          <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-6">Quick Insights</h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {insights.map((insight, index) => {
              const Icon = insight.icon;
              return (
                <div
                  key={index}
                  className="p-6 bg-gradient-to-br from-gray-50 to-white dark:from-slate-700 dark:to-slate-800 rounded-xl border border-gray-200 dark:border-slate-600 hover:shadow-md transition-shadow"
                >
                  <Icon className={`w-8 h-8 ${insight.color} mb-4`} />
                  <h4 className="text-lg font-bold text-gray-900 dark:text-white mb-2">
                    {insight.title}
                  </h4>
                  <p className="text-sm text-gray-600 dark:text-gray-400 leading-relaxed">
                    {insight.description}
                  </p>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
}
